@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.App_creatorTask,.App_panelInfo{display:flex;flex-direction:column;width:40vw;margin:20px;padding:20px;background-color:rgba(240,248,255,0.041);border-bottom:3px solid rgba(104,104,104,0.24);border-right:3px solid rgba(104,104,104,0.089);border-radius:5px;transition:.1s}.App_creatorTask--button,.App_panelInfo--task--button{color:#fff;background-color:rgba(83,88,94,0.041);border:1px solid #2196f3;border-radius:2px;cursor:pointer;transition:.2s}.App_creatorTask--button:hover,.App_panelInfo--task--button:hover{background-color:#282c34}.App_panelInfo--task--date,.App_panelInfo--task--date--finish{font-size:0.6em;-webkit-text-decoration:normal;text-decoration:normal;text-align:left}.App{min-height:100vh;background-color:#282c34;color:#fff;font-family:Quicksand, sans-serif;text-align:center;font-size:16px}.App_title{padding:5vh 0;color:#2196f3;transition:.1s}.App_title:hover{color:#fff}.App_containers{display:flex;justify-content:center}@media all and (max-width: 800px){.App_containers{flex-direction:column}}.App_creatorTask{height:350px}.App_creatorTask:hover{background-color:rgba(240,248,255,0.06)}@media all and (max-width: 800px){.App_creatorTask{margin:0 auto;width:90%;height:auto}}.App_creatorTask--title{margin:20px}@media all and (max-width: 400px){.App_creatorTask--title{font-size:1em;margin:10px}}@media all and (max-width: 400px){.App_creatorTask--containerForm{display:none}}.App_creatorTask--plus{display:none;font-size:3em;color:#ffffff;text-shadow:2px 2px 0px rgba(51,51,51,0.356863),5px 4px 0px rgba(0,0,0,0.247);cursor:pointer;transition:text-shadow .1s}.App_creatorTask--plus:hover{text-shadow:1px 1px 0px rgba(51,51,51,0.356863),3px 2px 0px rgba(0,0,0,0.4)}@media all and (max-width: 400px){.App_creatorTask--plus{display:block}}.App_creatorTask--input{width:80%;margin:10px auto;padding:10px 20px;border:none;border-bottom:1px solid #2196f3;border-radius:2px;background:#282c34;color:#fff}@media all and (max-width: 400px){.App_creatorTask--input{width:100%}}.App_creatorTask--paragraph{width:60%;margin:0 auto;padding:10px 20px}.App_creatorTask--paragraph--input{margin:15px;padding:20px;border:none;border-bottom:1px solid #2196f3;border-radius:2px;background:#282c34;color:#fff}.App_creatorTask--button{width:20%;margin:0 auto;padding:10px}@media all and (max-width: 420px){.App_creatorTask--button{width:50%;margin:auto}}@media all and (max-width: 800px){.App_panelInfo{margin:20px auto;width:80%;min-height:200px}}.App_panelInfo:hover{background-color:rgba(240,248,255,0.06)}.App_panelInfo--task{display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;padding:20px;border-radius:3px}.App_panelInfo--task--container{display:flex;justify-content:space-between;width:100%}.App_panelInfo--task--text{transition:.2s;cursor:pointer}.App_panelInfo--task--text:hover{color:#2196f3}.App_panelInfo--task--description{margin-top:5px;text-align:left;font-size:0.7em}.App_panelInfo--task--button{margin:6.66667px;min-width:25px;height:25px}.App_panelInfo--task--date{color:#fff}.App_panelInfo--task--date--finish{width:100%;color:#2196f3}.App_panelInfo--task:hover{background-color:rgba(240,248,255,0.075)}

